<style scoped lang="less">
    .pages {
        text-align: left;
        padding: 0 20px;
        .form {
            width: 40%;
            margin-top: 30px;
        }
    }

</style>


<template>
    <div class="pages">
        <H3>基础信息</H3>
        <div class="line"></div>
        <el-form class="form"  ref="form" :model="form" label-width="120px">
            <el-row>
                <el-form-item label="手机号" prop="account">
                    <el-input :disabled="!accountIsEdit" v-model="form.mobile" placeholder="请输入"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="pwd">
                    <el-input v-model="form.password" auto-complete="off" type="password"
                              placeholder="请输入"></el-input>
                </el-form-item>
                <el-form-item label="确认密码" prop="pwd2">
                    <el-input v-model="form.confirm_pass" auto-complete="off" type="password"
                              placeholder="请输入"></el-input>
                </el-form-item>
            </el-row>
            <el-row>
                <el-form-item label="真实姓名">
                    <el-input v-model="form.real_name" placeholder="请输入"></el-input>
                </el-form-item>
                <el-form-item label="角色">
                    <el-select style="width: 500px" v-model="selectRoles" multiple
                               placeholder="请选择">
                        <el-option
                                v-for="item in adminRoles"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="状态">
                    <el-select v-model="form.status" placeholder="请选择">
                        <el-option
                                label="启用"
                                :value="1">
                        </el-option>
                        <el-option
                                label="不启用"
                                :value="0">
                        </el-option>
                    </el-select>
                </el-form-item>
            </el-row>
            <el-row v-show="showDetails">
                <el-form-item label="创建时间">
                    {{form.create_time}}
                </el-form-item>
                <el-form-item label="最后登录时间">
                    {{form.login_time}}
                </el-form-item>
                <el-form-item label="最后登录Ip">
                    {{form.login_ip}}
                </el-form-item>
            </el-row>
            <el-form-item>
                <el-button type="primary" :loading="isLoading" @click="onSubmit">提交</el-button>
                <el-button @click="back">返回</el-button>
            </el-form-item>
        </el-form>
        <div>


        </div>
    </div>
</template>

<script>
    import api from '../../js/api/admin'

    export default {
        name: "add",
        data() {
            return {
                form: {
                },
                isLoading: false,
                isNoEdit: true,
                accountIsEdit: false,
                showDetails: false,
                adminRoles: [],
                selectRoles: [],
            }
        },
        mounted() {
            if (this.$route.query.id !== undefined) {
                this.isNoEdit = true;
                this.accountIsEdit = false;
                this.showDetails = true;
                this.loadData();
            } else {
                this.isNoEdit = false;
                this.accountIsEdit = true;
            }
            this.loadRoles();

        },
        methods: {
            onSubmit() {
                this.form.role_id = this.selectRoles;
                this.isLoading = true;
                api.assignUserRole(this.form, res => {
                    this.isLoading = false;
                    this.$message.success(res.data.message);
                    this.$router.push({name: 'admin'});
                    this.$route.meta.keepAlive = false;
                }, () => {
                    this.isLoading = false;

                })
            },
            back() {
                this.$router.back();
            },
            edit() {
                this.isNoEdit = false;
            },
            loadData() {
                api.detail({id: this.$route.query.id}, res => {
                    this.form = res.data.data;
                    this.form.password=null;
                    this.setSelect();
                    console.log(this.form)
                })
            },
            setSelect() {
                if (this.form.role != null) {
                    this.form.role.forEach(value => {
                        this.selectRoles.push(value.id);
                    })
                }
            },
            loadRoles() {
                api.spinner(res => {
                    this.adminRoles = res.data.data;
                })
            }
        }
    }
</script>
